
body {
  font-family: 'Inter', apple-system, BlinkMacSystemFont, sans-serif;
}

a, a:visited, a:active {
  color: #000;
}

a:hover {
  text-decoration: underline;
}

p {
  line-height: 1.5;
}

/*
NAV STYLES
*/

nav {
  margin: 2rem 3rem 6rem 3rem;
  display: flex;
  justify-content: space-between;
}

@media screen and (max-width: 800px) {
  nav {
    margin: 2rem 2rem 6rem 2rem;
  }

  nav .collapse {
    display: none;
  }
}

nav ul {
  list-style: none;
  margin: 0 0;
  padding: 0 0;
}

nav ul li {
  display: inline-block;
  margin-right: 20px;
}
nav ul li:last-child {
  margin-right: 0;
}

nav ul li a,
nav ul li a:visited {
  text-decoration: none;
  color: #000;
  font-weight: 500;
}

nav ul li a:hover {
  text-decoration: underline;
}

.hero {
  display: flex;
  align-items: center;
  width: 70vw;
  margin: 0 auto 6rem auto;
}

@media screen and (max-width: 800px) {
  .hero {
    flex-direction: column-reverse;
    text-align: center;
    width: auto;
    margin: 0 2rem 6rem 2rem;
  }
}

.columns {
  width: 80vw;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-column-gap: 1rem;
}

@media screen and (max-width: 800px) {
  .columns {
    width: auto;
    margin: 0 2rem;
    display: block;
  }

  .columns > div {
    margin-bottom: 2rem;
  }
}

h1 {
  font-size: 40px;
  flex: 1;
  font-weight: 600;
}

@media screen and (max-width: 800px) {
  h1 {
    font-size: 25px;
  }
}

h1 span {
  font-weight: 900;
}

h2 {
  font-weight: 600;
}

h2 a,
h2 a:visited,
h2 a:hover {
  color: #000;
  text-decoration: none;
}

#pattern {
  height: 30vw;
  width: 30vw;
  position: relative;
  /*transform: rotateX(60deg) rotateY(0deg) rotateZ(-45deg);*/
}

.pattern-container {
  padding: 0 20px;
}

.info {
  width: 80vw;
  margin: 6rem auto;
}

@media screen and (max-width: 800px) {
  .info {
    width: auto;
    margin: 6rem 2rem;
  }
}

footer {
  margin-top: 10rem;
  border-top: 1px dotted #ccc;
  text-align: center;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
}
